






























import Vue from 'vue'
import { wait, randint } from './utils'

export default Vue.extend({
  name: 'App',
  components: {},
  data() {
    return {
      start: false,
      end: false,
      control: false,
      difficty: 0,
      index: 0,
      cells: ['green', 'red', 'yellow', 'blue'], 
      frequencies: [329.63, 261.63, 220, 164.81],
      array: [] as string[],
      blacklight: '',
      audioCtx: new window.AudioContext()
    }
  },
  methods: {
    async show() {
      const timeout = [1500, 1000, 400][this.difficty]
      await wait(1000)

      for (const oldCell of this.array) {
        this.choose(oldCell)
        await wait(timeout)
      }

      const newCell = this.cells[randint(0, 3)]
      this.choose(newCell)
      this.array.push(newCell)

      this.control = true
    },
    click(event: MouseEvent) {
      const cell = (event.target as HTMLTextAreaElement).id
      this.choose(cell)

      if (!this.control) return
      if (this.array[this.index] == cell) {
        this.index++

        if (this.array.length == this.index) {
          this.index = 0
          this.control = false
          this.show()
        }
      } else {
        this.end = true
      }
    },
    async choose(id: string) {
      if (this.audioCtx) {
        const gain = this.audioCtx.createGain()
        gain.connect(this.audioCtx.destination)
        gain.gain.value = .2

        const oscillator = this.audioCtx.createOscillator()
        oscillator.type = 'square'
        oscillator.frequency.value = this.frequencies[this.cells.indexOf(id)]
        oscillator.connect(gain)

        oscillator.start()
        oscillator.stop(this.audioCtx.currentTime + .2)
      }
      
      this.blacklight = id

      await wait(200)
      this.blacklight = ''
    },
    restart() {
      this.start = false
      this.end = false
      this.index = 0
      this.control = false
      this.array = []
    }
  }
});
